import { LOGIN_SUCCESS, LOGIN_FAILED, USER_LOADED_SUCCESS, USER_LOADED_FAILED } from '../types'


const REACT_APP_API_URL = 'http://localhost:8000/api/'





const load_user = () => async dispatch =>{

    const accessToken = localStorage.getItem('access')

    if(accessToken){


        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `JWT ${accessToken}`
        }

        try{

            const data = await fetch(`${REACT_APP_API_URL}auth/users/me/`, {method: 'GET', headers}).then(res => res.json())

            if(data.name && data.email && typeof data.id === 'number'){


                dispatch({
                    type: USER_LOADED_SUCCESS,
                    payload: data
                })

            }else{
                dispatch({
                    type: USER_LOADED_FAILED
                })
            }


        }catch(err){

            console.log(err);
            dispatch({
                type: USER_LOADED_FAILED
            })
        }

    }else{

        dispatch({
            type: USER_LOADED_FAILED
        })
    }
}

const login = (email, password) => async dispatch =>{

    const headers = {
        "Content-Type": 'application/json'
    }

    const body = JSON.stringify({ email, password })


    try{
        const data = await fetch(`${REACT_APP_API_URL}auth/jwt/create/`,{ method:'POST', headers, body }).then(res => res.json())
            
        const { access, refresh, detail } = data

        // console.log(data);

        if(access && refresh){

            dispatch({
                type: LOGIN_SUCCESS,
                payload: {access, refresh}
            })

            dispatch(load_user())

            return LOGIN_SUCCESS

        }
        else if(detail){

            dispatch({
                type: LOGIN_FAILED
            })
            
            console.log(detail);

            return detail
        }
        else{
            dispatch({
                type: LOGIN_FAILED
            })
        }
    }
    catch(err){

        console.log(err);

        dispatch({
            type:LOGIN_FAILED
        })
    }
} 




export { login, load_user }