import { LOGIN_SUCCESS, LOGIN_FAILED, SIGNUP_SUCCESS, SIGNUP_FAILED, VERIFY_LOGIN_SUCCESS, VERIFY_LOGIN_FAILED, 
         USER_LOADED_SUCCESS, USER_LOADED_FAILED } from '../types'


const initialState = {
    access: null,
    user: null,
    is_authenticated: false
}


function reducer(state=initialState, { payload, type }){


    switch(type){

        case LOGIN_SUCCESS:

            localStorage.setItem('access', payload.access)

            return { ...state, access: payload.access }

        
        case USER_LOADED_SUCCESS:

            return {...state, user: payload}

        
        case USER_LOADED_FAILED:
        case LOGIN_FAILED:

            localStorage.removeItem('access')

            return initialState

        default:

            return state
    }
}


export default reducer